	import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
	export default {
		components: {
			mpvueCityPicker
		},
		data() {
			return {
        province:[],
        city:[],
        area:[],
				editType:'edit',
				id:'',
				name:'',
				tel:'',
				detailed:'',
				isDefault:false,
				cityPickerValue: [0, 0, 1],
				themeColor: '#007AFF',
				region:{label:"请点击选择地址",value:[],cityCode:""},
				// province:[],
				// city:{},
				// area:{}
			};
		},
		methods: {
   //     fetchAreInfo(){
   //      let url = "config/regionInfo";
   //      this.$Request.get(url).then( res => {
   //        if(res && res.code == 200){
   //          this.province = res.data.province;
   //          this.city = res.data.city;
   //          this.area = res.data.district;
			// console.log(this.province,this.city,this.area);
   //          // console.log( this.provinceDataList,this.cityDataList,this.areaDataList);
   //          // this.addInfo = res.data;
   //        }
   //      })
   //    },
			onCancel(e) {
				console.log(e)
			},
			chooseCity() {
				this.$refs.mpvueCityPicker.show()
			},
			onConfirm(e) {
				this.region = e;
				this.cityPickerValue = e.value;
			},
			isDefaultChange(e){ 
        if(this.id && this.isDefault == false){
          uni.showModal({
            title: '提示',
            content: '你将设置为默认收货地址',
            success: (res)=>{
              if(res.confirm){
                this.isDefault = e.detail.value;
                let url = "address/setDefaultAddress";
                let data = {id:this.id};
                this.$Request.post(url,data).then( res => {
                  uni.showToast({
                    title:res.msg,
                    icon:"none"
                  })
                })
              }
            }
          })
        }
			},
			del(){
				uni.showModal({
					title: '删除提示',
					content: '你将删除这个收货地址',
					success: (res)=>{
						if (res.confirm) {
							// uni.setStorage({
							// 	key:'delAddress',
							// 	data:{id:this.id},
							// 	success() {
							// 		uni.navigateBack();
							// 	}
							// })
              let url = "address/delAddressInfo";
              let data = {"id":this.id};
              this.$Request.post(url,data).then( res => {
                if(res && res.code == 200){
                   uni.navigateBack();
                 }
              });
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
				
			},
			save(){
				let data={"name":this.name,"head":this.name.substr(0,1),"tel":this.tel,"address":{"region":this.region,"detailed":this.detailed},"isDefault":this.isDefault}
				if(this.editType=='edit'){
					data.id = this.id
				}
				if(!data.name){
					uni.showToast({title:'请输入收件人姓名',icon:'none'});
					return ;
				}
				if(!data.tel){
					uni.showToast({title:'请输入收件人电话号码',icon:'none'});
					return ;
				}
				if(!data.address.detailed){
					uni.showToast({title:'请输入收件人详细地址',icon:'none'});
					return ;
				}
				if(data.address.region.value.length==0){
					uni.showToast({title:'请选择收件地址',icon:'none'});
					return ;
				}
				uni.showLoading({
					title:'正在提交'
				})
				//实际应用中请提交ajax,模板定时器模拟提交效果
				setTimeout(()=>{
					uni.setStorage({
						key:'saveAddress',
						data:data,
						success() {
							uni.hideLoading();
							uni.navigateBack();
						}
					})
				},300)
			}
		},
		onLoad(e) {
           // this.fetchAreInfo();
			//获取传递过来的参数
			this.editType = e.type;
			if(e.type=='edit'){
				uni.getStorage({
					key:'address',
					success: (e) => {
						this.id = e.data.id;
						this.name = e.data.username;
						this.tel = e.data.mobile;
						this.detailed = e.data.address
						this.isDefault = e.data.is_default;
						// this.cityPickerValue = e.data.address.region.value;
						this.region.label = e.data.province +"-"+ e.data.city +"-"+ e.data.district;
					}
				})
			}
		},
		onBackPress() {
			if (this.$refs.mpvueCityPicker.showPicker) {
				this.$refs.mpvueCityPicker.pickerCancel();
				return true;
			}
		},
		onUnload() {
			if (this.$refs.mpvueCityPicker.showPicker) {
				this.$refs.mpvueCityPicker.pickerCancel()
			}
		}
	};
